<template>
  <Layout id="app">
    <Header class="app-header">
      <img v-if="!mobile" class="logo" src="favicon.png" alt="logo"/>
      <h1 class="app-name">{{ name }}</h1>
      <Menu mode="horizontal" theme="dark" :active-name="activeMenuItem" @on-select="handleClickMenuItem">
        <Submenu v-if="mobile" name="pages">
          <Icon type="ios-browsers-outline" :size="24" slot="title"/>
          <MenuItem name="page-home">主页</MenuItem>
          <MenuItem name="page-res">资源管理器</MenuItem>
          <MenuItem name="page-role">角色管理器</MenuItem>
          <MenuItem name="page-node">节点管理器</MenuItem>
        </Submenu>
        <templete v-else>
          <MenuItem name="page-home">主页</MenuItem>
          <MenuItem name="page-res">资源管理器</MenuItem>
          <MenuItem name="page-role">角色管理器</MenuItem>
          <MenuItem name="page-node">节点管理器</MenuItem>
        </templete>
        <Submenu name="file">
          <Icon v-if="mobile" :size="24" type="ios-document-outline" slot="title"/>
          <span v-else slot="title">文件</span>
          <MenuItem name="file-save">保存</MenuItem>
          <MenuItem name="file-load">
            <Upload action="" :format="['json']" :before-upload="importData">载入</Upload>
          </MenuItem>
        </Submenu>
      </Menu>
    </Header>
    <Content class="content">
      <component :is="component"/>
    </Content>
  </Layout>
</template>

<script>

  import NodeManager from "@/components/nodes/NodeManager";
  import {Layout, Header, Content, Modal, Input, MenuItem, Menu, Submenu, Upload, Icon} from 'view-design';
  import {mapActions, mapState} from 'vuex';
  import ResourceManager from "@/components/resources/ResourceManager";
  import RoleManager from "@/components/roles/RoleManager";
  import Home from "@/components/Home";

  export default {
    name: 'app',
    components: {Home, RoleManager, ResourceManager, NodeManager, Layout, Header, Content, Modal, Input, MenuItem, Menu, Submenu, Upload, Icon},
    data() {
      return {
        page: 'page-home',
        activeMenuItem: '',
      }
    },
    computed: {
      component() {
        switch (this.page) {
          case "page-home": return Home;
          case "page-res": return ResourceManager;
          case "page-role": return RoleManager;
          case "page-node": return NodeManager;
          default: return Home;
        }
      },
      ...mapState(['name', 'mobile'])
    },
    methods: {
      handleClickMenuItem(itemName) {
        if (itemName.startsWith('page-')) {
          this.page = itemName;
        }
        else if (itemName.startsWith('file-')) {
          if (itemName.substring(5) === 'save') {
            this.save();
          }
        }
        this.activeMenuItem = itemName;
      },
      importData(file) {
        this.load(file);
      },
      ...mapActions([
        'save',
        'load',
      ]),
    },
  }
</script>

